import React from 'react';
import Helmet from 'react-helmet';
import styled from 'styled-components';
import Rehype from 'rehype-react';

import Header from '../components/Header';
import Nav from '../components/Nav';
import Request from '../components/Request';
import Sidebar from '../components/Sidebar';

const renderAst = new Rehype({
  createElement: React.createElement,
  components: { request: Request },
}).Compiler;

const Documentation = styled.main`
  float: left;
  width: 55%;
  margin-left: 20%;
  padding: 2em 4em;

  @media ${props => props.theme.mobile} {
    width: 100%;
    margin: 0;
  }
`;

const DocContents = styled.div`
  /**
   * Code 
   */
  code {
    background: #f4f7fb;
    padding: 0 0.25em;
    font-size: 0.95em;
    border-radius: 3px;
    font-family: ${props => props.theme.monspace};
  }

  .gatsby-highlight pre {
    background: #f9fbfd;
    border: 1px solid #ececec;
    border-radius: 3px;
    padding: 0.5em 1em;
    overflow: auto;
    margin: 0 0 1em 0;
  }

  .gatsby-highlight code {
    background: none;
    color: inherit;
    padding: 0;
    border-radius: 0;
  }

  /**
   * Tables
   */
  table {
    border: 1px solid #ececec;
    overflow: hidden;
    border-collapse: separate;
    border-spacing: 0;
    width: 100%;
    margin: 1em 0;
    border-radius: 3px;
  }

  th {
    font-weight: 600;
  }

  h3 {
    margin: 0.5em 0;
  }

  th:empty {
    display: none;
  }

  td,
  th {
    text-align: left;
    padding: 0.375em 0.75em 0.375em 0.75em;
  }

  td {
    line-height: 1.75em;
    vertical-align: top;
  }

  .bordered-table table {
    border-width: 0 1px 1px 0;
  }

  .bordered-table td,
  .bordered-table th {
    border: 1px solid #ececec;
    border-right: 0;
    border-bottom-width: 0;
  }

  .fixed-table table {
    table-layout: fixed;
  }

  .attributes-table td {
    line-height: 1.5em;
  }

  .attributes-table td:first-child {
    text-align: right;
    line-height: 1em;
  }

  .attributes-table strong {
    white-space: nowrap;
  }

  small {
    color: #888;
    display: block;
    margin-top: 0.25em;
    font-size: 0.875em;
    font-weight: 400;
    line-height: 1em;
  }

  img {
    display: block;
    margin: 2rem auto;
    max-width: 600px;
    width: 98%;
  }
`;

const DocHeader = styled.header`
  padding: 0 0 0.25em 0;
  margin-bottom: 1.5em;
  border-bottom: 2px solid #f0f0f0;

  h1 {
    margin-top: 0;
    margin-bottom: 0.5em;
  }
`;

export default ({ pathContext, location }) => {
  const { page, nav } = pathContext;
  const description = page.frontmatter.description ? (
    <p>{page.frontmatter.description}</p>
  ) : (
    ''
  );

  return (
    <div>
      <Helmet>
        <title>{page.frontmatter.title} &middot; Sixgill </title>
      </Helmet>
      <Header currentPath={location.pathname} fixed />
      <Nav nav={nav} currentPath={location.pathname} />
      <Documentation>
        <DocHeader>
          <h1>{page.frontmatter.title}</h1>
          {description}
        </DocHeader>

        <DocContents>
          { renderAst(page.htmlAst) }
        </DocContents>
        <Sidebar headings={page.headings} />
      </Documentation>
    </div>
  );
};



